* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
  transition: .3s ease;
}
body{
    background-color: #f99417;
}

.container-box {
  margin: auto;
  margin-top: 2rem;
  width: 60%;
  display: flex;
  justify-content: center;
  align-items: center;
  border-radius: 1rem;
  background-color: #363062;
  box-shadow:inset 5px 5px 15px #363062;
}
.heading {
  color: #f99417;
  font-size: 1.5rem;
  padding: 1rem;
  text-align: center;
}
.input-display {
  width: 100%;
  margin: auto;
  padding: 0.6rem;
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.input-screen {
  width: 100%;
  font-size: 1rem;
  padding: 0.5rem;
  color: #f99417;
  font-weight: 500;
  border-radius: 10px;
  align-items: center;
  background-color: #4d4c7d;
  box-shadow: 3px 2px 5px #323145;
}
.range-box {
  width: 90%;
  padding: .5rem;
  margin: auto;
  display: flex;
  justify-content: center;
  align-items: center;

}
#range-input{
  width: 60%;
}
.btn-button {
  color: #363062;
  font-weight: 700;
  font-size: 1rem;
  padding: 0.5rem;
  align-items: center;
  background-color: #f99417;
  box-shadow: 3px 2px 5px #323145;
}
label {
  color: #f99417;
  align-items: center;
  margin: 6px;
  padding: .3rem;
  font-size: 1rem;
}
.pass-styles {
    padding: .5rem;
    display: flex;
    justify-content: space-around;
    align-items: center;
    flex-wrap: wrap;
}
.box-random{
    width: 45%;
    transition: .3s ease;
}
.input-btn {
  width: 100%;
  padding: 1rem 0;
  display: flex;
  justify-content: space-around;
  align-items: center;
  flex-wrap: wrap;
  padding-bottom: 2rem;
}



 @media (min-width:591px) and (max-width:767px) {
    .box-random{
        width: 60%;
        transition: .3s ease;
    }
    .heading{
        margin-top: 1rem;
        font-size: 1rem;
        font-weight: 500;
    }
    label{
        font-size: .8rem;
        font-weight: 400;
    }
}

@media (min-width:481px) and  (max-width:590px) {
  .container-box{
    width: 90%;
  }
    .box-random{
        width: 75%;
        transition: .3s ease;
    }
    .heading{
        margin-top: 1rem;
        font-size: 1rem;
        font-weight: 500;
    }
    label{
        font-size: .7rem;
        font-weight: 500;
    }
  
} 
@media (min-width:300px) and (max-width:480px) {
  .container-box{
    width: 100%;
  }
  .box-random{
      width: 100%;
      transition: .3s ease;
  }
  .heading{
      margin-top: 1rem;
      font-size: .9rem;
      font-weight: 400;
  }

  .range-box {
      width: 90%;
      margin: auto;
      display: flex;
      justify-content: center;
      align-items: center;
    }
  label{
      font-size: .6rem;
      font-weight: 400;
  }
}

